.UserList {
  display: flex;
  flex-direction: column;
  padding: 25px 30px;

  .User {
    display: flex;
    flex-direction: column;
    row-gap: 5px;
    padding: 10px;
    background-color: #F3F3F3;
    margin-top: 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 13px;
    color: #000000;

    .user_title {
      font-style: normal;
      font-weight: 400;
      font-size: 13px;
      line-height: 13px;
      color: #A3A3A3;
      margin-right: 5px;
    }

    .about_user {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .about_user_link {
        font-weight: 400;
        font-size: 13px;
        line-height: 14px;
        color: #4B51EF;
        text-decoration: none;
      }
    }
  }
}